import request from '@/utils/axios'


//批次水平列表
export function batchList(params) {
  return request({
    url: '/batch/array',
    method: 'post',
    params
  })
}


//批次列表
export function ccList(params) {
  return request({
    url: '/batch/score/array',
    method: 'post',
    params
  })
}

//批次，场次列表
export function commit(params) {
  return request({
    url: '/batch/score/commit',
    method: 'post',
    params
  })
}

//获取发射信息 (用于选择那次发射是违规发射)
export function setfire(params) {
  return request({
    url: '/batch/score/fire',
    method: 'post',
    params
  })
}

//获取场次成绩(违规)详情
export function fire(params) {
  return request({
    url: '/batch/score/get',
    method: 'post',
    params
  })
}


//批次水平新增
export function add(params) {
  return request({
    url: '/batch/add',
    method: 'post',
    params
  })
}

//批次修改
export function modify(params) {
  return request({
    url: '/batch/modify',
    method: 'post',
    params
  })
}


//删除个人违规信息
export function remove(params) {
  return request({
    url: '/batch/score/user/delete',
    method: 'post',
    params
  })
}











