<template>
  <div class="fillcontain">
    <h3>违规数据管理</h3>
    <div class="contain">
      <div class="table_container">
        <el-row class="readWrapper"></el-row>
        <el-table
          v-loading="loading"
          :data="tableData"
          border
          stripe
          highlight-current-row
          header-cell-class-name="table-header-class"
          style="width:100%"
        >
          <el-table-column label="序号" align="center" width="50px">
            <template slot-scope="scope">
               <span>{{scope.$index+(paginations.pageIndex-1) * paginations.pageSize + 1}} </span>
            </template>
          </el-table-column>
          <el-table-column property="batch_name" label="批次" align="center"></el-table-column>
          <el-table-column property="submit" label="批次状态" width="100px" :formatter="pcState" align="center"></el-table-column>
          
          <el-table-column
            property="updateTime"
            label="时间段"
            :formatter="formatTime"
            align="center"
          ></el-table-column>
          <el-table-column fixed="right" label="操作">
            <template slot-scope="scope">
              <el-button @click="editClick(scope.row)" type="primary" size="small">查看录入情况</el-button>
              <!-- <el-button @click="handleClick(scope.row)" type="primary" size="small">解锁</el-button> -->
            </template>
          </el-table-column>
        </el-table>
        <el-row>
          <el-col :span="24">
            <div class="pagination">
              <el-pagination
                v-if="paginations.total > 0"
                :page-sizes="paginations.pageSizes"
                :page-size="paginations.pageSize"
                :layout="paginations.layout"
                :total="paginations.total"
                :current-page="paginations.pageIndex"
                @current-change="handleCurrentChange"
                @size-change="handleSizeChange"
              ></el-pagination>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>
    <el-dialog title="详情" :visible.sync="dialogTableVisible">
      <el-table :data="gridData">
        <el-table-column property="name" label="名字" width="100"></el-table-column>
        <el-table-column property="type" label="状态" width="270" :formatter="formatter"></el-table-column>
        <el-table-column fixed="right" label="操作">
            <template slot-scope="scope">
              <el-button :v-show="scope.type" @click="remove(scope.row)" type="primary" size="small">删除</el-button>
            </template>
          </el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
import { ccList, setfire, commit,remove } from "@/api/batch";
export default {
  data() {
    return {
      valueDate: "",
      nameSearch: "",
      value: "",
      form: {
        ball: 0,
        height: 0
      },
      weapon: {
        10: 0,
        12: 0,
        15: 0,
        255: 0
      },
      gridData:[],
      user: this.sessionData("get", "user"),
      dialogFormVisible2: false,
      formLabelWidth: "120px",
      tableData: [],
      optionsFS: [],
      about: "",
      loading: false,
      dialogTableVisible:false,
      paginations: {
        total: 0, // 总数
        pageIndex: 1, // 当前位于哪页
        pageSize: 10, // 1页显示多少条
        pageSizes: [5, 10, 15, 20], //每页显示多少条
        layout: "total, sizes, prev, pager, next, jumper" // 翻页属性
      },
      selectId: ""
    };
  },
  mounted() {
    this.ccList({
      count: this.paginations.pageSize,
      index: this.paginations.pageIndex,
      search: {
        state: 1
      }
    });
  },
  methods: {
    remove(row){
      var data= {
        uid:row.id,
        _id:row._id,
      }
      remove(data).then(res => {
        if (res.code == 0) {
          this.$message({
            message: "违规成绩删除成功！",
            type: "success"
          });
          this.dialogTableVisible = false;
        }
      });
    },
    pcState(row){
      return row.submit?"已归档":"未归档";
    },
    handleClick(i) {},
    handleAreaChange(val) {
      this.form.fire = val;
    },
    add() {
      var data = {
        _id: this.selectId,
        uid: this.user._id,
        weapon: this.weapon,
        foul: {
          height: this.form.height,
          ball: this.form.ball,
          report: this.form.report,
          fire: this.form.fire || [],
          other:Number(this.form.other || 0),//this.form.other.constructor == String? this.form.other.split(","): this.form.other
        },
        about: this.about
      };
      commit(data).then(res => {
        if (res.code == 0) {
          this.$message({
            message: "违规数据录入成功！",
            type: "success"
          });
          this.dialogFormVisible2 = false;
        }
      });
    },

    formatter(row){
      return row.type?"已录":"未录";
    },
    setfire(id) {
      this.selectId = id;
      setfire({
        _id: id,
        uid: this.user._id
      }).then(res => {
        if (res.code == 0) {
          var list = this.$unfly(res.data);
          console.log("违规发射", list);
          list.forEach(i => {
            i.info = i.miss + "--" + this.$tool.formatDate(new Date(i.time));
          });
          this.dialogFormVisible2 = true;
          this.optionsFS = list;
        }
      });
    },

    formatTime(row) {
      row.timeD =
        this.$tool.formatDate(new Date(row.start)) +
        "--" +
        this.$tool.formatDate(new Date(row.end));
      return row.timeD;
    },

    editClick(row) {
      var arr = [];
      for(var i in row.score){
        row.score[i]._id = row._id;
        arr.push(row.score[i])
      }
      this.gridData = arr;
      this.dialogTableVisible = true;
    },

    ccList(obj) {
      var data = this.$tool.deep(obj);
      data.index -= 1;
      ccList(data).then(res => {
        if (res.code == 0) {
          var list = this.$unfly(res.data);
          this.loading = false;
          this.paginations.total = list.total;

          var user = list.users;
          list.array.forEach(i => {
            i.name = "";
            i.loser.forEach(j => {
              i.name += user[j];
              i.name += " ";
            });
            i.name += "VS ";
            i.winner.forEach(j => {
              i.name += user[j];
              i.name += " ";
            });
          });
          console.log("88888888888888",list)
          this.tableData = list.array;
          this.paginations.total = list.total;
        }
      });
    },

    // 每页多少条切换
    handleSizeChange(pageSize) {
      this.paginations.pageSize = pageSize;
      this.ccList({
        count: this.paginations.pageSize,
        index: this.paginations.pageIndex,
        search: {
          state: 1
        }
      });
    },
    // 上下分页
    handleCurrentChange(page) {
      this.paginations.pageIndex = page;
      this.ccList({
        count: this.paginations.pageSize,
        index: this.paginations.pageIndex,
        search: {
          state: 1
        }
      });
    }
  }
};
</script>

